import evbus from "@/evbus";

type Handler<T> = (payload: any) => Promise<T>;

export const _loadNotifError = <T> (description: string, handler: Handler<T>) => {
  return async function (payload: any) {
    let result: any = undefined;
    let msg: string = '';
    evbus.$emit("loading", true);
    try {
      result = await handler(payload);
      return result;
    } catch (err) {
      msg = `Gagal ${description}`;
      evbus.$emit(`action-fail`, { msg });
      throw err;
    } finally {
      evbus.$emit("loading", false);
    }
  }
};

export const _action = <T> (name: string, description: string, handler: Handler<T>) => {
  return async function (payload: any) {
    let result: any = undefined;
    let msg: string = '';
    evbus.$emit("loading", true);
    try {
      result = await handler(payload);
      msg = `Sukses ${description}`;
      evbus.$emit(`action-success`, { msg });
      return result;
    } catch (err) {
      msg = `Gagal ${description}`;
      evbus.$emit(`action-fail`, { msg });
      throw err;
    } finally {
      evbus.$emit("loading", false);
    }
  }
}