import axios from "./axios";
import { Node, Edge, Point } from "./interfaces";
import { _action, _loadNotifError } from "./action";

export class Repo<T> {

  constructor (
    protected readonly url: string,
    protected readonly name: string
  ) {}

  create = _action(`create-${this.name}`, `menambah data ${this.name}`, async (payload) => {
    delete payload._id;
    const response = await axios.post(this.url, payload);
    return response.data;
  });

  update = _action(`update-${this.name}`, `mengubah data ${this.name}`, async (payload) => {
    const _url = `${this.url}/${payload.id}`;
    const response = await axios.put(_url, payload.body);
    return response.data;
  });

  delete = _action(`delete-${this.name}`, `menghapus data ${this.name}`, async (payload) => {
    const _url = `${this.url}/${payload.id}`;
    console.log('_url=', _url);
    await axios.delete(_url);
  });

  find = _loadNotifError(`Memuat data ${this.name}`, async (payload) => {
    const response = await axios.get(this.url);
    return response.data;
  });

  findOne = async (payload: any) => {
    const _url = `${this.url}/${payload.id}`;
    const response = await axios.get(_url);
    return response.data;
  };

}

class NodeRepo extends Repo<Node> {
  createIntersection = _action("add-intersection", "menambah titik", async (payload) => {
    const data = {
      latitude: payload.point[0],
      longitude: payload.point[1],
      jabatan: 'intersection'
    };
    const response = await axios.post(this.url, data);
    return response.data;
  });

  find = _loadNotifError(`Memuat data ${this.name}`, async (payload) => {
    const response = await axios.get(this.url, {
      params: {
        jabatan: payload.jabatan
      }
    });
    return response.data;
  });
}

class EdgeRepo extends Repo<Edge> {}

const BASE_URL = process.env.VUE_APP_API_URL;
export const node = new NodeRepo(`${BASE_URL}/nodes`, 'node');
export const edge = new EdgeRepo(`${BASE_URL}/edges`, 'edge');
